import React from 'react'
import PropTypes from 'prop-types'
import { StaticQuery, graphql } from 'gatsby'
import Header from './header'
import Footer from './footer'

const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={() => (
      <>
        <div className="container">
          <Header />
          <div style={{ width: `100%`, maxWidth: `1024px`, margin: `0 auto`, padding: '60px 30px 30px 30px', background: 'linear-gradient(to bottom, #ffffff, rgba(0, 0, 0, 0))', boxSizing: 'border-box', borderRadius: '20px 20px 0 0' }}>
            {children}
          </div>
          <Footer />
        </div>
      </>
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
